<template>
    <li class="coupon-item" @click="tab()">
        <div class="logo">
            <img :src="item.thumb"/>
        </div>
        <div class="inner">
            <div class="title">{{item.couponname}}</div>
            <div class="text">
                <!--{{item.backstr}}￥<span>{{item.deduct}}</span>-->
                {{item.backstr}}￥<span>{{item.backmoney}}</span>
            </div>
        </div>
        <div class="right">立即使用
        </div>
    </li>
</template>

<script>

    export default {
        data() {
            return {

            }
        },
        props:{
            item:{
                type:Object,
//              required:true
            }
        },
        methods: {
            tab(){
//                console.log('tab')
                this.$emit('tab')
            }
        },
        components:{

        },
        mounted(){

        },
        created(){

        }
    }
</script>

<style lang="less" scoped>
    @import '../../../assets/less/index.less';
    .coupon-item {
        background:#fff;
        border-radius:.075rem;
        display:-webkit-flex;
        display:flex;
        position:relative;
        margin-bottom:.1rem;
        .logo {
            width: .72rem;
            height: .72rem;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                display: block;
                width: .52rem;
                height: .52rem;
            }
        }
        .inner {
            flex: 1;
            padding: .1rem .1rem .1rem 0;
            text-align: left;
            .title {
                position:relative;
                font-size:.15rem;
                color:#444;
                white-space:nowrap;
                text-overflow:ellipsis;
                overflow:hidden;
                height:.25rem;
                line-height:.25rem;
            }
            .text {
                position:relative;
                font-size:.13rem;
                color:#ff6c00;
                height:.25rem;
                line-height:.25rem;
                span {
                    font-size: .2rem;
                }
            }

        }
        .right {
            width:.45rem;
            position:relative;
            display:-webkit-flex;
            display:flex;
            -webkit-align-items:center;
            align-items:center;
            box-sizing: border-box;
            padding: .125rem;
            font-size:.13rem;
            /*display:block;*/
            line-height:.14rem;
            color:#666;

        }

    }

</style>


